#velocity {
  @include transform(translateY(-100%));
  background: $black;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: $above-content;
}

#header {
  background: $primary-color;
  position: relative;
  z-index:999;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  min-height: 122px;
  -webkit-box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);
  -moz-box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);
  box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);

  @include breakpoint(tablet){
    padding-top: 20px;
    min-height: 100%;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
  }
}

  .uppercontainer{
    height: 77px;
    position: relative;
    max-width:1200px;
    margin:0 auto;
    //border-top: 1px solid $white;

    @include breakpoint(tablet) {
      height: 110px;
      padding-bottom: 40px;
    }
  }

  // REmove?
  .lowercontainer {
    width: 100%;
    background: $lightest-grey;
    display: none;

    @include breakpoint(tablet) {
      position: absolute;

      top: 100%;
      bottom:0;
      left:0;
      height: 100px;
      width: 367px;
      background: $dark-blue;
      -webkit-box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);
      -moz-box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);
      box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);
    }
  }

  .free{
    display:none;
    @include breakpoint(tablet){
      display: block;
      position: absolute;
      top:50%;
      @include transform(translateY(-50%));
      left:20px;
      height: auto;
      z-index: 3;
    }
    @include breakpoint(laptop){
      float: right;
      position: relative;
      right:0;
      left:auto;
      background: $lightest-grey;
      padding:14px 15px 14px;
      top:0;
      @include transform(translateY(0));

    }
  }

  .et-dropdown-wrapper {
    display: block;
    position: relative;
    right: 0;
    width: 113px;
    height: 77px;
    float:right;
    background: $white;

    .et-nav-trigger {
      border: 2px solid $primary-color;
      border-radius: 50px;
      background: transparent;
      color: $black;
      padding: 5px 10px;
      text-transform: uppercase;
      @include font-size(1.3);
      font-weight:600;
      top: 50%;
      left: 50%;
      position: absolute;
      @include transform(translateY(-50%)translateX(-50%));

      @include breakpoint(tablet){
        padding: 5px 25px 5px 25px;
        top: 50%;
        left: auto;
        right: 20px;
        @include transform(translateY(-50%)translateX(0));
      }

      @include breakpoint(laptop){
       display: none;
      }
    }

    @include breakpoint(tablet){
      position: absolute;
      margin:0;
      right:35px;
      left:35px;
      top:60px;
      z-index: $above-content;
      height: 60px;
      width: auto;
      padding:70px 0 0 0;
      -webkit-box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);
      -moz-box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);
      box-shadow: 0px 18px 94px -16px rgba(0,0,0,0.38);
    }

    @include breakpoint(laptop){
      display: none;
    }
  }

  // Mobile Nav dropdown
  .et-dropdown {
    top:-100%;
    background: $black;
    height: 100%;
    left: 0;
    position: fixed;
    width: 100%;
    padding: 0 2.5rem;
    z-index: $above-content;

    h2 {
      @include font-size(1.8);
      @include line-height(6);
      color: $white;
      height: $header-s;
      padding: 0;
    }

    @include breakpoint(laptop) {
      top: 75px;
      left: 50%;
      @include transform(translateX(-50%));
      max-width: 1200px;
      margin: 0 auto;
      padding:0 15px 0 0;
      position: absolute;
      width: 100%;
      height: 70px;
      display: block;
      z-index: $content;
      background: transparent;

      h2 {
        display: none;
      }

      li {
        display: inline-block;
      }
    }

  }

  .et-dropdown-content, .et-dropdown-content ul {
    @include transition(all .3);
    margin: 0;
    padding: $header-s 0 0;
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    overflow-x: hidden;
    overflow-y: hidden;

    li, ul li {

      &:last-of-type>a{
        border-bottom-width: 1px;
        border-style: solid;
        border-color: darken($dark-grey, 15);
      }

      @include breakpoint(laptop) {

        &:last-of-type>a{
          border: 0;
        }
      }

    }

    &.is-hidden{
      @include transform(translateX(100%));

      @include breakpoint(laptop) {
        @include transform(translateX(0));
        //@include transform(translateY(30%));
        //@include transition(all .25s);
        opacity: 0;
        visibility: hidden;

        &.visible-desktop{
          opacity: 1;
          visibility: visible;
        }

        &.service-in{
          opacity: 1;
          visibility: visible;
        }
      }
    }

    // global menu hover links
    a, ul, ul a {
      @include transform(translateZ(0));
      @include backface-visibility(hidden);
      opacity: 1;
      @media only screen and (max-width: 991px) {
        @include transition(all .3s cubic-bezier(0.755, 0.050, 0.855, 0.060));
      }
    }

    a, ul a {

      padding:20px 25px;
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      border-style: solid;
      border-color: darken($dark-grey, 15);
      border-top-width: 1px;
      border-left:0;
      border-right:0;
      border-bottom: 0;

      @include breakpoint(laptop) {
        padding:7px 0 0 0;
        border:none;
      }

    }

    .menu-item-has-children {

      & > a {
        position: relative;
        &::before, &::after {
          content: '';
          width: 9px;
          height: 2px;
          position: absolute;
          background: $primary-color;
          top:50%;
          @include transform(translateX(-50%));
          @include backface-visibility(hidden);
          right: 0;
        }

        &::before {
          top: 33px;
          right: 25px;
          @include transform(rotate(-45deg));
        }

        &::after {
          top: 28px;
          right: 25px;
          @include transform(rotate(45deg));
        }

        @include breakpoint(laptop) {
          padding: 23px 30px 23px 20px;
          &::after, &::before {
            @include transition(all .3s);
            margin-top:-1px;
            width: 6px;
            top:35px;
            background: $dark-grey;
          }

          &::before {
            right:19px;
            @include transform(rotate(45deg));
          }

          &::after {
            right:15px;
            @include transform(rotate(-45deg));
          }
        }
      }
    }

    &.move-out>li>a{
      @include transform(translateX(-100%));
      opacity: 0;
    }

    //todo: Add color to header
    .go-back {
      position: relative;
      a {
        padding-left: 40px;

        &::before, &::after {
          content: '';
          width: 9px;
          height: 2px;
          position: absolute;
          background: $primary-color;
          top:50%;
          @include transform(translateX(-50%));
          left:0;
        }

        &::before {
          top: 33px;
          left: 25px;
          @include transform(rotate(45deg));
        }

        &::after {
          top: 28px;
          left: 25px;
          @include transform(rotate(-45deg));
        }
      }
      @include breakpoint(laptop) {
        display: none;
      }

    }

    .sub-title {
      a {
        background-color:$b;
        color:$g;
        padding:5px 15px;
        @include font-size(1.4);
      }
      @include breakpoint(laptop) {
        display: none;
      }
    }

  }

