  //close button


  .navbar-brand {
    //Todo: get height from customizer for all device sizes?
    position: relative;
    display:block;
    width:auto;
    height: 100%;
    > img {
      height: 100%;
      width: auto;
    }

    &:hover, &:focus{
      background: transparent;
    }

    @include breakpoint(tablet){
      position: relative;
      float: left;
      padding: 0 0 0 15px;
      z-index: $above-content;
    }
  }

  .et-nav-meta{
    top:0;
    padding: 0 20px;
    width: 100%;
    display: block;
    height: 45px;
    background: $dark-green;

    a, p {
      @include font-size(1.4);
      color: $white;
    }

    @include breakpoint(tablet){
      padding:0 30px;
      width: 50%;
      position: relative;
      float: right;
      height:100%;
      background: transparent;
    }

    @include breakpoint(laptop) {
      padding: 0 35px;
    }

  }

  .meta-desc {

      p{
        text-align: center;
        padding:5px;
        @include font-size(1.4);
      }
      @include breakpoint(tablet){
        color:$white;
        display: block;
        height: 100%;
        position: relative;

        p{
          position: absolute;
          text-align: left;
          width: 100%;
          bottom: 10px;
          //@include transform(translateY(-50%));
          left: 0;
          display:block;
          @include font-size(1.3);
          padding: 0 0 0 20px;
        }
      }
  }

  .meta-search {
    position: absolute;
    display: block;
    height: 45px;
    width: 45px;
    right: 0;
    top: 0;
    overflow: hidden;

    a{
      width: 100%;
      display: block;
      height: 100%;

      &:focus {
        color: $white;
      }
    }
    i{
      @include center();
      @include font-size(1.7);
      position: absolute;
      line-height: 0;
      opacity: 1;
    }
    @include breakpoint(tablet){
      float: right;
      margin:0;
      top:50%;
      @include transform(translateY(-50%));
      right: 125px;
      width: 70px;
      height: 70px;
    }
    @include breakpoint(laptop){
      position: relative;
      float: right;
      margin:0;
      top:0;
      right:0;
      height: 70px;
      width: 70px;
      @include transform(translateY(0));
      z-index: $content;
      a{
        color:$white;
        background: $dark-grey;
        &:hover{
          color:$white;
          background: $dark-blue;
        }
        &:focus{
          background: $dark-blue;
        }
      }
    }
  }

  .meta-wrapper {

    @include breakpoint(tablet) {
      position: absolute;
      display: block;
      width: 100%;
      height: auto;
      right: 35px;
      top: 50%;
      @include transform(translateY(-50%));
    }
  }

  .meta-phone{
    float:left;
    padding:10px 0 0;
    @include breakpoint(tablet){

      position: relative;
      display: table;
      float:right;
      padding:0;

      a{
        @include font-size(1.2);
        color:$white;
        display: block;
        position: relative;
        font-weight: 600;
      }

      &.no-social {
        a{
          padding-right: 0;
          margin-right: 0;
          border-right: none;
        }
      }
    }
  }


.navbar {
  #header.logged-in & {
    margin-top: 32px;
  }
}

.nav-social-widget {
  display: none;
  @include breakpoint(tablet){
    display: block;
    float: right;
    position: relative;
    padding-left: 20px;

    a{
      @include font-size(1.2);
      padding:0 5px;
      color:$white;
    }
  }
}